#titleProduct{
  height: 220px;
  min-height: none;
  user-select: none;
  overflow: hidden;
}
#titleProductImg{
  width: 200%;
  height: 100%;
  margin: auto;
}
#titleProductDiv{
  height: 100%;
  color: #FFF;
  background-color: rgba(26, 26, 26, .65);
  backdrop-filter: blur(1px);
  z-index: 1;
  padding-left: 10%;
}
#titleProductDiv hr{
  border-top-width: 1px;
  opacity: .9;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, .5);
  width: 75%;
}
#titleProductWord{
  font-size: 3em;
  letter-spacing: 3px;
  text-shadow: 2px 2px 1px rgba(0, 0, 0, .5);
  opacity: .9;
}

#categoryContain{
  gap: 1rem;
}
.categoryList{
  max-width: 280px;
  height: 183px;
  overflow: hidden;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0.5);
}
.categoryList:hover{
  transform: translate(-4px, -4px);
  box-shadow: 4px 4px 0px rgba(0, 0, 0, 0.8);
}
.categoryList .categoryListImg{
  z-index: 1;
}
#categoryAbu, #categoryContain{
  background-color: #BFBFBF;
}
#categoryAbu{
  right: 50%;
}
.categoryListTitle{
  z-index: 3;
  background-color: rgba(250, 250, 250, .5);
  backdrop-filter: blur(3px);
  user-select: none;
}
a.page-link {
  cursor: pointer;
}
@media (min-width: 576px) {
  #titleProduct{
    height: 220px;
    min-height: none;
  }
  #titleProductWord{
    font-size: 3em;
  }
  #titleProductImg{
    width: 200%;
  }
  .categoryList{
    max-width: 300px;
    height: 196px;
  }
}
@media (min-width: 768px) {
  #titleProductImg{
    width: auto;
  }
}
@media (min-width: 992px) {
  #categoryContain{
    max-width: 820px;
  }
  .categoryList{
    max-width: 360px;
    height: 235px;
  }
  #titleProduct{
    height: 320px;
  }
}
@media (min-width: 1200px) {
  .categoryList{
    max-width: 400px;
    height: 261px;
  }
  #categoryContain{
    max-width: 910px;
  }
}
@media (min-width: 1400px) {
}
@media (min-width: 2400px) {
  #titleProductImg{
    width: 100%;
  }
}