#aboutUs{
  height: 220px;
  min-height: none;
  user-select: none;
  overflow: hidden;
}
#aboutUsImg{
  width: 200%;
  height: 100%;
  margin: auto;
}
#aboutUsDiv{
  height: 100%;
  color: #FFF;
  background-color: rgba(26, 26, 26, .65);
  backdrop-filter: blur(1px);
  z-index: 1;
  padding-left: 10%;
}
#aboutUsDiv hr{
  border-top-width: 1px;
  opacity: .9;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, .5);
  width: 75%;
}
#aboutUsWord{
  font-size: 3em;
  letter-spacing: 3px;
  text-shadow: 2px 2px 1px rgba(0, 0, 0, .5);
  opacity: .9;
}

