#askaraExpTitle{
  font-weight: 700;
  font-size: 36px;
  line-height: 44px;
}
#askaraWord {
  word-break: break-all;
}
#s4AboutH2, #vmTitle, #sorgTitle, #bodTitle, #sumClientTitle{
  font-weight: 700;
  font-size: 26px;
  /* line-height: 50px; */
  /* letter-spacing: 1.5px; */
  padding-left: .5rem;
  padding-right: .5rem;
}
#s4Container{
  background-color: #BFBFBF;
}
.s4About{
  transform: translateY(-15px);
  opacity: 0;
  transition: all .6s cubic-bezier(0.47, 0, 0.745, 0.715);
}
.s4About.scrolled{
  transform: translateY(0);
  opacity: 1;
}
.s4AboutTitle{
  font-weight: 700;
  font-size: 64px;
  line-height: 78px;
}
.s4AboutWord{
  word-break: break-all;
}
#bodFlex{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  column-gap: 2rem;
  row-gap: 7rem;
  padding-bottom: 7rem;
}
.bodFlexContain{
  width: auto;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.bodItem{
  width: 300px;
  height: 350px;
}
.bodWord{
  bottom: 0;
  width: 80%;
  left: 50%;
  transform: translate(-50%, 50%);
  z-index: 2;
  background-color: #e7e7e7;
}
#sumClientDiv{
	background: #DEDEDE;
}
#sumClientSplide{
	padding: 1rem 2.5rem;
	background-color: #DEDEDE;
}
#sumClientSplide .splide__arrow{
	opacity: 1;
	background: transparent;
	border-radius: none;
}
#sumClientSplide .splide__slide img {
  object-fit: contain;
}
#sumClientSplide .splide__arrow--prev {
	left: 0.2rem;
}
#sumClientSplide .splide__arrow--next {
	right: 0.2rem;
}
#sumClientSplide img{
	-webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
  filter: grayscale(100%);
}
#sumClientSplide img:hover{
	-webkit-filter: grayscale(0); /* Safari 6.0 - 9.0 */
  filter: grayscale(0);
}

@media (min-width: 576px) {
