  #s4AboutH2, #vmTitle, #sorgTitle, #bodTitle, #sumClientTitle{
    font-weight: 700;
    font-size: 40px;
  }
  #sumClientSplide .splide__arrow--prev {
    left: -1.25rem;
  }
  #sumClientSplide .splide__arrow--next {
    right: -1.25rem;
  }
}
@media (min-width: 768px) {
  .bodItem{
    width: 320px;
    height: 373px;
  }
  #aboutUsImg{
    width: auto;
  }
  #sumClientSplide{
    padding: 2rem 3rem;
    background-color: #DEDEDE;
  }
}
@media (min-width: 992px) {
  #aboutUs{
    height: 320px;
  }
}
@media (min-width: 1200px) {
  #s4AboutH2, #vmTitle, #sorgTitle, #bodTitle, #sumClientTitle{
    font-weight: 700;
    font-size: 45px;
    letter-spacing: 1.5px;
  }
}
@media (min-width: 1400px) {
}
@media (min-width: 2400px) {
  #aboutUsImg{
    width: 100%;
  }
}